*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    width: 50%;
    margin: 0 auto;
}
.form{
    width: 50%;
    margin: 0 auto;
}
.container h1{
    text-align: center;
    padding: 10% 0px 20px;
}

label{
    display: block;
}

input{
    box-sizing: border-box;
    height: 35px;
    width: 100%;
    border: none;
    border: 1px solid rgb(118, 116, 116);
   background-color: rgb(255, 255, 255);
   border-radius: 5px;
   padding: 5px;
   margin-top: 3px;
}

.password label{
margin-top: 10px;
}

button{
    width: 100px;
    height: 30px;
    border-radius: 5px;
    border: none;
    margin: 10px 0px 15%;
    font-size: 14px;
    padding: 5px 0px;
    background-color:#415167 ;
    color: aliceblue;

}
.button{
text-align: end;
}
.signUp{
    text-align: center;
}
.signUp .link{
    color: black;
    text-decoration: none;
}
.signUp .link:hover{
    color: rgb(87, 87, 87);
    text-decoration: underline;
}

