.containerHome{
  width: auto;
  margin: -10px;
  padding: 0;
}
.header{
  display: flex;
  height: 700px;
  justify-content: flex-end;
}

.header img{
width: auto;
height: 100%;
/* position: relative; */
z-index: 0;
}
.rectangl{
background: #C7A17A;
width: 20%;
height: 100%;
position: relative;
}

.title{
  position: absolute;
  left: 8%;
  bottom: 10%;
  align-self: flex-end;
  z-index: 2;
  line-height: 25px;
}
.title h2{
margin: 15px;
font-weight: 900;
font-size: 54px;
color: #415167;
}
.title  .white{
  display: block;
padding: 25px 10px;
color: #F9FBFF;
background: #C7A17A;
}

.menu{
  position: relative;
  height: 70px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color:#415167 ;
  z-index: 3;
}
.menu .link{
  font-size: 22px;
color: #F9FBFF;
text-decoration: none;
}
.menu .link:hover{
  text-decoration: underline;
  }



@media screen and (max-width:820px){
  .header img{
      width: 100%;
      }
  .rectangl{
      position: absolute;
      width: 100%;
      height: 20%;
      z-index: 2;
  }

  .title h2{
      display: inline-block;
      margin: 3px 0px;
      padding: 25px 10px;
     background: #F9FBFF;
     font-size: 45px;
  }
  .title{
      bottom: -5%;
  }
}