.contentProfile {
    margin: 0;
    padding: 0;
    margin-top: 60px;
}

 .text {
    text-align: center;
    color: #415167;
}

.text p {
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 15px;
}

.text h1 {
    font-weight: 900;
    font-size: 30px;
}

.cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    text-align: center;
    margin-top: 60px;
}

.cards-item {
    width: 226px;
}

.cards-item h3 {
    font-weight: 700;
    font-size: 24px;
    color: #232C38;
}

.cards-item p {
    color: #151D28;
} 